<template>
  <div class="fill-height">
    <app-bar title="Histórico">
      <template v-slot:append>
        <v-btn icon to="/recad" replace plain>
          <v-icon>mdi-arrow-left</v-icon>
        </v-btn>
      </template>
    </app-bar>
    <async-container :loading="loading">
      <v-data-table
        :headers="headers"
        :items="items"
        :items-per-page="10"
        :footer-props="{'items-per-page-options': [10]}"
        :dark="$store.state.dark"
        disable-sort
        :mobile-breakpoint="320"
        class="elevation-2"
        no-data-text="Nenhum recadastamento encontrado"
      >
        <template v-slot:item.hora="{item}">{{parseDate(item.hora)}}</template>
      </v-data-table>
    </async-container>
  </div>
</template>

<script>
  import AppBar from '@/components/AppBar';
  import AsyncContainer from '@/components/AsyncContainer';
  import AppWebClient from '@/http/AppWebClient';
  import moment from 'moment';
  export default {
    name: 'Historico',
    components: {AsyncContainer, AppBar},
    data: () => ({
      loading: true,
      items: null,
      headers: [
        {value: 'id', text: 'Protocolo'},
        {value: 'hora', text: 'Data/Hora'},
      ],
    }),
    async created() {
      try {
        const appWebClient = new AppWebClient();
        this.items = await appWebClient.getRecadastramentos();
        this.loading = false;
      } catch (e) {
        await this.$router.replace('/recad');
      }
    },
    methods: {
      parseDate(datetime) {return moment(datetime).format('DD/MM/YYYY HH:mm:ss')},
    },
  }
</script>

